<template>
 <div class="tag-cloud">
    <h1 class="tc">Catagories</h1>
  <div class="pill" v-for="tag in uniqueTags" :key="tag">
       <router-link :to="{name:'Tag',params:{tag:tag}}">{{tag}}</router-link>
  </div>
 </div>
</template>

<script>
import { ref } from '@vue/reactivity';
export default {
  props:["posts"],
  setup(props){
    let tags= ref([]);
    props.posts.forEach((post) => {
      post.tags.forEach((tag)=>{
     
        tags.value.push(tag);
      })
    });

   let uniqueTags = tags.value.filter((tag,index,array)=>{
      return array.indexOf(tag) === index
    })

    return {uniqueTags}
  }
}
</script>

<style>
.tag-cloud{
  padding: 10px;
}
.tag-cloud h1{
  border-bottom: 3px solid black;
  padding: 16px 8px;
  color: black;
  font-weight: bold;
}

.tag-cloud a{
  color: black;
  text-decoration: none;
}



.pill{
        display: inline-block;
        margin: 10px 10px 0 0;
        color: #444;
        background: #ddd;
        padding: 8px;
        border-radius: 20px;
        font-size: 14px;
    }
</style>