<template>
   <form @submit.prevent="addPost">
      <label>Eng Title</label>
      <input type="text" v-model="engtitle" required/>

      <label>MM Title</label>
      <input type="text" v-model="mmtitle" required/>

      <label>Artical (eng)</label>
      <textarea v-model="engcontent" @keydown.enter.prevent="eckeydown"></textarea>
      
        <div class="bodycontainer" v-for="engcontent in engcontents" :key="engcontent">
              {{engcontent}}
        </div>
      
      <label>Artical (mm)</label>
      <textarea v-model="mmcontent" @keydown.enter.prevent="mmckeydown"></textarea>

        <div class="bodycontainer" v-for="mmcontent in mmcontents" :key="mmcontent">
            {{mmcontent}}
        </div>

      <label>Image Url(  )</label>
      <input type="text" v-model="img" required/>

      <div class="img-box" v-if="img">
          <img :src="img" :width="400" :height="300" alt="">
      </div>

      <label>youtube video link</label>
      <input type="text" v-model="video" />

      <label>Tags(hit enter to add a tag)</label>
      <input type="text" v-model="tag" @keydown.enter.prevent="handleKeydown"/>
       <div class="pill" v-for="tag in tags" :key="tag">
          {{tag}}
       </div>
      <button class="my-3">add post</button>
  </form>

  <div class="container-fluid ii">
      <div class="row">

          <h3 class="text-center">Website  </h3>
      
      <div class="col-8 img-boxone mx-auto my-2">
          <img :src="imgpointone"  alt="">
      </div>

      <div class="col-8 img-boxone mx-auto my-2">
          <img :src="imgpointtwo"  alt="">
      </div>

      <div class="col-8 img-boxone mx-auto my-2">
          <img :src="imgpointthree"  alt="">
      </div>
    
  </div>
  </div>
</template>

<script>
import { ref } from '@vue/reactivity'
import {db, timestamp} from '../firebase/config'
import { collection , addDoc} from "firebase/firestore"; 
import {useRouter} from 'vue-router'
export default {
    setup(){
        let router = useRouter();
        
        let engtitle=ref('');
        let mmtitle=ref('');
        let engcontent = ref('');
        let engcontents = ref([]);
        let mmcontent = ref('');
        let mmcontents = ref([]);
        let tag = ref('');
        let tags = ref([]);
        let img = ref('');
        let imgpointone = ref('https://drive.google.com/uc?id=1zTkcwRvKm90ikjHOJq9mqAb3fHLGQ7Lu');
        let imgpointtwo = ref('https://drive.google.com/uc?id=1tQSI3-GFczKNWn5x3sUdawyoV3Wxx7nt');
        let imgpointthree = ref('https://drive.google.com/uc?id=1xw7T7iptt1Xc1F3x783eANHOx4Cq_7_Y');
        let video = ref('');

        let handleKeydown = ()=>{
            if(!tags.value.includes(tag.value)){
                tags.value.push(tag.value)
            }
            tag.value = ""
        }

        let eckeydown = ()=>{
            if(!engcontents.value.includes(engcontent.value)){
                engcontents.value.push(engcontent.value)
            }

            engcontent.value = ""
        }

        let mmckeydown = ()=>{
            if(!mmcontents.value.includes(mmcontent.value)){
                mmcontents.value.push(mmcontent.value)
            }

            mmcontent.value = ""
        }

        let addPost =async ()=>{

            let newPost ={
                         engtitle:engtitle.value,
                         engcontents:engcontents.value,
                         mmtitle:mmtitle.value,
                         mmcontents:mmcontents.value,
                         img:img.value,
                         tags:tags.value,
                         video:video.value,
                         created_at: timestamp
            }
         let res = await addDoc(collection(db, "posts"),newPost);
            //  redirect user to home page 
            router.push('/');
        }

        return {
        engtitle,
        engcontent,
        engcontents,
        eckeydown,
        mmtitle,
        mmcontent,
        mmcontents,
        mmckeydown,
        tag,
        tags,
        handleKeydown,
        video,
        addPost,
        img,
        imgpointone,
        imgpointtwo,
        imgpointthree
        
        }
    }
}
</script>

<style>
     form{
        max-width: 480px;
        margin: 0 auto;
        text-align: left;
    }
    input,textarea{
        display: block;
        margin: 10px 0;
        width: 100%;
        box-sizing: border-box;
        padding: 10px;
        border: 1px solid #eee;
    }
    textarea{
        height: 160px;

    }
    label{
        display:inline-block;
        margin-top: 30px;
        position: relative;
        font-size: 20px;
        color: black;
        margin-bottom: 10px;
    }
    
    button{
        display: block;
        margin-top: 30px;
        background: #eee;
        color: red;
        font-weight: bold;
        border: none;
        padding: 8px 16px;
        font-size: 18px;
    }
    .pill{
        display: inline-block;
        margin: 10px 10px 0 0;
        color: #444;
        background: #ddd;
        padding: 8px;
        border-radius: 20px;
        font-size: 14px;
    }

    .img-box{
        width:300px;
        height: 300px;
    }

    .ii{
        margin-top: 100px;
    }

    .img-boxone{
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .img-boxone img{
        width: 1000px;
        height: 600px;
    }

    .bodycontainer{
        max-height: 500px;
        background-color: #ddd;
        color: red;
        margin:20px;
        overflow: scroll;
        overflow-x: hidden;
    }

    @media(max-width:600px){
        .img-boxone img{
            width: 350px;
            height: 200px;
        }
    }
</style>