<template>
<div class="container-fluid">
    <div class="row">
  <div v-if="post" class="post col-11 mx-auto">
                
                <div class="col-11 mx-auto">
                    <button class="ms-auto" @click="changeMM">
                        <span v-if="changemm">Eng</span>
                        <span v-else>MM</span>
                    </button>
                </div>

                <div class="img-container mx-auto col-12">
            
                    <img :src="post.img"   alt="">
           
                </div>
                <div class="text-center my-3">
                    <h2 class="pt" v-if="changemm">{{post.mmtitle}}</h2>
                    <h2 class="pt" v-else>{{post.engtitle}}</h2>
                    
            
                </div>

                <div>
                    <div v-if="changemm">
                        <p class="pb" v-for="mmcontent in post.mmcontents" :key="mmcontent">{{mmcontent}}</p>
                    </div>
                    <div v-else>
                        <p class="pb" v-for="engcontent in post.engcontents" :key="engcontent">{{engcontent}}</p>
                        
                    </div>
                </div>

                <div class="col-6 video-container my-3 mx-auto">
                    <iframe class="viframe" :src="post.video" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                   
                </div>
        
    
      
    
      <hr/>
        <div class="pill" v-for="tag in post.tags" :key='tag'>
            <router-link :to="{name:'Tag',params:{tag:tag}}">{{tag}}</router-link>
        </div>

 
        
        
  </div>
   <div v-else>
      Loading...
  </div>
    </div>
    
</div>

 
</template>

<script>
import getPost from '../composables/getPost'
import {useRoute} from 'vue-router'
import { ref } from '@vue/reactivity';
// import { onMounted } from '@vue/runtime-core';
// import { computed } from '@vue/runtime-core';


// import { ref, onMounted } from 'vue';


export default {
    props:['id'],
    setup(props){
        let route = useRoute();
        let changemm = ref(false);
        // let parabody = props.body;
        
        
        
       
        
       
        // this.$router.params.id  this keyword is not working in compositon api
        // so we use route function (route.params.id)
        let {post,error,load} = getPost(route.params.id);

        load();
        let changeMM = ()=>{
            changemm.value = !changemm.value
        };

        
        
        
        
        //    box      
        //Create    array   Detail  Looping 

        //    eng   10  

     
    
        return {post,error,changemm,changeMM}
    }
}
</script>

<style scoped>
    a{
        text-decoration: none;
    }

    .post{
        /* margin: 0 40px 30px; */
        padding-bottom: 30px;
        border-bottom: 3px dashed #e7e7e7;
        text-align: justify;
    }
    .post h2{
         display: inline-block;
        background-color: #eee;
        position: relative;
        font-size: 50px;
        color: black;
        margin-bottom: 10px;
        /* max-width: 400px; */
     
    }
    .post p{
        color: black;
    }
  
    .pill{
        display: inline-block;
        margin: 10px 10px 0 0;
        color: #444;
        background: #ddd;
        padding: 8px;
        border-radius: 20px;
        font-size: 14px;
        
    }

    .pill a{
        color: black;
        text-decoration: none;
    }

    .img-container,.video-container{
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .img-container img{
         width: 560px;
         height: 315px;
    }

    .viframe{
        width:500px;
        height:300px;
    }

    .pb{
        text-indent: 100px;
    }

    .pb:nth-last-of-type(1),.pb:nth-last-of-type(2),.pb:nth-last-of-type(3){
        text-align: right;
        text-indent: 0px;
    }

    .pb:nth-last-of-type(3){
        margin-top: 100px;
    }

    .pb:nth-last-of-type(1){
        margin-left: 50%;
        text-align: justify;
        margin-bottom: 50px;
        
    }
    

     @media(max-width:600px){
        .img-container img{
            width: 400px;
            height: 200px;
        }

        .viframe{
            width:400px;
            height: 200px;
        }

        .post h2{
            font-size: 28px;
        }

        
    }

    
    
</style>