<template>
<router-link :to="{name:'Detail',params:{id:post.id,body:post.body,bodymm:post.bodymm,a:post.a,b:post.b}}">
     <div class="post">
         <div class="col-12 img-container">
            <img :src="post.img" width="300" height="200" alt="">
            
         </div>
         <div>
         
            <h2>{{post.engtitle}}</h2>     
            
         
        </div>   
            <div>
                <p>{{cutPostBody}}</p>
            </div>
            <div class="pill" v-for="tag in post.tags" :key='tag'>
                <router-link :to="{name:'Tag',params:{tag:tag}}">{{tag}}</router-link>
            </div>
     </div>
</router-link>
</template>

<script>
import { computed } from '@vue/runtime-core';
export default {
    props:['post'],
    setup(props){
        let cutPostBody = computed(()=>{
            return props.post.engcontents[0].substring(0,100)+"...";
        })
        return {cutPostBody}

        
    }
}
</script>

