    }
    .post h2{
        display: inline-block;
        /* background-color: black; */
        position: relative;
        font-size: 50px;
        color: black;
        margin-bottom: 10px;
        max-width: 400px;
        
     
    }
    .post p{
        color: black;
    }

    .img-container{
        display: flex;
        justify-content: left;
